import Topbar from '../../components/topbar'
import GoodsItem from '../../components/goods-item'
import {uniLoadMore} from '@dcloudio/uni-ui'

export default {
	components: {
    Topbar, GoodsItem, uniLoadMore
  },
	data() {
		return {
			// 左右滑动单个项目宽度
      goodsItemWidth: 280,
			// 首页数据
			homeData: {},
			// 附近店铺
			nearStoreInfo: {},
			// tabs
      tabActive: 0,
      tabs: [{
        id: 5,
        name: 'HEA',
        list: [],
        page: 0
      },
      {
        id: 4,
        name: '银鳞堂',
        list: [],
        page: 0
      },
      {
        id: 3,
        name: 'HE75 DENIM',
        list: [],
        page: 0
      }],
      // 加载中
      loading: 'more',
			pages: {}
		}
	},
	computed: {
    slides () {
      return this.homeData.bannerList
    },
    activityBanner () {
      return this.homeData.activity_banner
    },
    categorys () {
      return this.homeData.index_categorys_items
    },
    shareList () {
      return this.homeData.shareList
    },
    activityPopup () {
      return this.homeData.activityPopup
    },
    boyChosen () {
      return this.homeData.boyChosen
    },
    holiday () {
      return this.homeData.holiday
    }
  },
	methods: {
		// 获取首页数据
    async getHomeData () {
      let _this = this
      let params = {}

      uni.showLoading({
				title: '加载中'
			})

      // 获取附近店铺
      let json = await _this.$http.get('o2o/getNearStores')
      if (json.code === 0) {
        this.nearStoreInfo = json.rs
        params.business_id = json.rs.business_id
      }

      _this.$http.get('index/v3', params).then(res => {
        uni.hideLoading()
				uni.stopPullDownRefresh()
        if (res.code === 0) {
          _this.homeData = res.rs.data
        } else {
					uni.showToast({
						title: res.msg,
						icon: 'none',
						duration: 2000
					})
        }
      })
    },
		// 商品列表
    getProductList () {
      let _this = this
      let active = _this.tabActive
      let tab = _this.tabs[active]
      let list = tab.list
      let page = tab.page
			
      // 检查是否可以加载
      if (_this.loading === 'loading' || _this.loading === 'noMore') {
        return false
      } else {
        page += 1
      }
      // loading
      _this.loading = 'loading'

      // ajax获取产品列表
      const params = {
        brand_id: tab.id,
        pageNo: page
      }
      _this.$http.get('product/productList', params).then(res => {
        if (res.code === 0 && res.rs.data.length > 0) {
          tab.list = [...list, ...res.rs.data]
          tab.page = page
          _this.tabs[active] = tab
					_this.pages = res.rs.pageList
					UTIL.pages(_this.pages, function (res) {
						if (res) {
							_this.loading = 'more'
						} else {
							_this.loading = 'noMore'
						}
					})
        } else {
          _this.tabs[active].page = page
					_this.loading = 'noMore'
        }
      })
    },
    // 切换标签
    changeTab (e) {
      const index = e.mp.detail.index
      const tab = this.tabs[index]
      this.tabActive = index
      this.loading = 'more'
      if (tab.list.length === 0) {
        this.getProductList()
      }
    }
	},
	onLoad() {
		this.getHomeData()
		this.getProductList()
	},
	onReachBottom () {
    this.getProductList()
  },
	onPullDownRefresh () {
		this.getHomeData()
	}
}
